import React from 'react';
import './style.css';



export default class ProfileHeader extends React.Component {

    constructor(){
        super();
        this.showHeaderAt = 80;
    }
    

    componentDidMount(){
        window.addEventListener('scroll', ()=>{this.handleScroll()});
    }

    render() {
        return (
            <header className="header-fixed">
                <div className="header-limiter">
                    <h1><a>Zemin <span>Wang</span></a></h1>
                    <nav>
                        {/* <a href="#about_section">ABOUT</a> */}
                        <a href="#experience_section">PORTFOLIO</a>
                        <a href="/zeminwang-resume.pdf">RESUME</a>
                        {/* <a href="#education_section">EDUCATION</a> */}
                        {/* <a href="#contact_section">CONTACT</a> */}
                    </nav>
                </div>
            </header>
        );
    }

    handleScroll(){
        //console.log('listen', window.scrollY, this.showHeaderAt)
        if(window.scrollY > this.showHeaderAt) {
            document.body.classList.add("fixed");
        }
        else {            
            document.body.classList.remove("fixed");
        }
        
    }
  }